.toggler{
    border: 2px solid black;
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-evenly;
    position: relative;
    left: 20px;
    list-style: none;
    max-width: 300px;
    padding-left: 0;
}

.menuItem{
    flex: 1 1 auto;
    text-align: center;
    position: relative;
}

.menuItem:hover{
    background-color: rgb(150, 192, 240);
}

.menuItem:focus{
    background-color: rgb(105, 168, 240);
}

.menuItem::selection{
    background-color: red;
}

.menuItem + .menuItem{
    border-left: 1px solid gray;
}