.attributeDiv{
    color: red;
    width: 350px;
    height: 200px;
    position: relative;
    border: 10px solid rgb(150, 192, 240);
    margin-right: 10px;
    margin-left: 10px;
    margin-bottom: 25px;
}

.attributeDiv:hover .attributeImage{
    filter:blur(4px);
}

.attributeDiv:hover .attributeText{
    display: block;
}

.attributeImage{
    position: absolute;
    max-width: 100%;
    max-height: 100%;
    object-fit: cover;
    height: 200px;
    width: 350px;
}

.attributeText{
    position:absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: none;
    width: 100%;
    text-shadow:
		-1px -1px 0 #000,
		1px -1px 0 #000,
		-1px 1px 0 #000,
		1px 1px 0 #000;
}

.subject{
    color: white;
    font-size: 30pt;
    text-align: center;

}

.subjectContent{
    font-size: 20pt;
    color: white;
    text-align: center;
    word-wrap: normal;
    width: 100%
}